import React from "react";
import Form from "react-bootstrap/Form";
import SchoolMap from '../components/SchoolMap';
import { useLocation, useHistory } from "react-router-dom";
import schoolsDump from "./../dump.json";
import SchoolTable from '../components/SchoolTable';
import LicenseComponent from "../components/LicenseComponent";
import PropTypes from "prop-types";
function titleCase(str) {
    return str.toLowerCase().split(' ').map(function(word) {
      return (word.charAt(0).toUpperCase() + word.slice(1));
    }).join(' ');
  }

function ReportsPage(props) {
  const { search } = useLocation();
  const history = useHistory();
  const recordings = s => s.pmn.haveRecordings / s.pmn.scheduled;
    const minutes = s => s.pmn.haveMinutes / s.pmn.scheduled;
    const recordingsSort = (s,t) => (recordings(t) + minutes(t)-recordings(s) - minutes(s));
  const value = (search && new URLSearchParams(search).get("district")) || "";
  const schools = schoolsDump.filter(s => {
      if(!value || value === "") {
          return true;
      }
      if(value.startsWith("sd")) {
          return parseInt(value.slice(2)) === s.UtahLeg.senate;
      }
      if(value.startsWith("hd")) {
          return parseInt(value.slice(2)) === s.UtahLeg.house;
      }
      return false;
  });
  const licenseStatus = schools.reduce((acc, s) => {
    Object.keys(acc).forEach(k => {
        acc[k] += s.licenseStatus[k] || 0;
    })
    return acc;
}, {
    "Expired": 0,
    "New": 0,
    "No License": 0,
    "Reinstated": 0,
    "Renewed": 0,
    "Student": 0,
    "All": 0
});
  const licenseTypes = schools.reduce((acc, s) => {
      Object.keys(acc).forEach(k => {
          acc[k] += s.licenseTypes[k] || 0;
      })
      return acc;
  }, {
    "1": 0,
    "1 - Returning": 0,
    "2": 0,
    "3": 0,
    "Alt. Route to Licensure": 0,
    "Associate": 0,
    "Level 1 APT": 0,
    "Level 1 LEA-Specific": 0,
    "Level 2 LEA-Specific": 0,
    "No License": 0,
    "Professional": 0,
    "Temporary": 0,
    "All": 0
  });
  const summarySchool = {
      licenseTypes,
      licenseStatus
  }
  const handleChange = (evt) => {
    history.push("?district=" + evt.target.value);
  };
  return (
    <div className="container">
      <Form>
        <Form.Group>
          <Form.Label>Legislative District</Form.Label>
          <Form.Control as="select" value={value} onChange={handleChange}>
            <option value="">All</option>
            {Array.from(Array(29).keys()).map((i) => (
              <option value={"sd" + (i + 1)}>
                {"Senate District " + (i + 1)}
              </option>
            ))}
            {Array.from(Array(75).keys()).map((i) => (
              <option value={"hd" + (i + 1)}>
                {"House District " + (i + 1)}
              </option>
            ))}
          </Form.Control>
        </Form.Group>
      </Form>
      {
          schools && schools.length > 0 && <SchoolMap recenter schools={schools} center={[schools[0].Lat, schools[0].Lng]}/>    
      }
      
      <SchoolTable summary sort={recordingsSort} schools={schools} columns={[{
                name: "City",
                func: s => titleCase(s.City)
            },
            {name: "Recordings Avail", func: recordings, summary: true},
            {name: "Minutes Avail", func: minutes, summary: true}
            
            ]}/>
            <LicenseComponent school={summarySchool}/>
    </div>
  );
}

ReportsPage.propTypes = {};

export default ReportsPage;
